import React, { useState, useEffect } from 'react';
import {Button, Placeholder} from "@vkontakte/vkui";
import Icon24ShareOutline from "@vkontakte/icons/dist/24/share_outline";
import Icon24StoryOutline from "@vkontakte/icons/dist/24/story_outline";
import Loader from "./loader";
import bridge from "@vkontakte/vk-bridge";
const images = [
    {label: 'aries', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172597.png'},
    {label: 'taurus', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172432.png'},
    {label: 'gemini', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172433.png'},
    {label: 'cancer', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172427.png'},
    {label: 'leo', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172428.png'},
    {label: 'virgo', img: 'https://24tv.ua/resources/photos/news/720x540_DIR/202005/1351979_14172434.png'},
    {label: 'libra', img: 'https://24tv.ua/resources/photos/news/720x522_DIR/202005/1351979_14172429.png'},
    {label: 'scorpio', img: 'https://24tv.ua/resources/photos/news/720x553_DIR/202005/1351979_14172435.png'},
    {label: 'sagittarius', img: 'https://24tv.ua/resources/photos/news/720x538_DIR/202005/1351979_14172437.png'},
    {label: 'capricorn', img: 'https://24tv.ua/resources/photos/news/720x574_DIR/202005/1351979_14172436.png'},
    {label: 'aquarius', img: 'https://24tv.ua/resources/photos/news/720x520_DIR/202005/1351979_14172430.png'},
    {label: 'pisces', img: 'https://24tv.ua/resources/photos/news/720x538_DIR/202005/1351979_14172431.png'}
];


const Horoscope = ({zodiac, user}) => {
    const canvasRef = React.useRef(null);
    const [hososcopeArr, setHoroscopeArr] = useState(null);
    useEffect(()=>{
        fetch('https://mysterious-garden-57052.herokuapp.com/horoscope/sex')
            .then(res=>res.json())
            .then(res=>{
                setHoroscopeArr(res)
            })
    }, []);
    const wallPost = () => {
        const link = "https://vk.com/app7617614";
        bridge.send("VKWebAppShowWallPostBox", {
            attachments: link,
            message:
                zodiac.label + " - " + hososcopeArr.source + "\n" +
                hososcopeArr.sign_teaser.find(item=>item.sign === zodiac.name).text +
                "\n",
            owner_id: user.id,
            copyright: link,
        });
    };

    function writeTop(text) {
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d')
        ctx.fillStyle = 'white';
        ctx.font = 'normal 27px Oswald';
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 6;
        let lineHeight = getFontHeight(ctx.font);
        let maxWidth = canvas.width;
        let marginTop = getFontHeight(ctx.font);
        let words = text.split(" ");
        let countWords = words.length;
        let line = "";
        for (let n = 0; n < countWords; n++) {
            let testLine = line + words[n] + " ";
            let testWidth = ctx.measureText(testLine).width;
            if (testWidth > maxWidth) {
                let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
                ctx.strokeText(line, marginLeft, marginTop);
                ctx.fillText(line, marginLeft, marginTop);
                line = words[n] + " ";
                marginTop += lineHeight;
            }
            else {
                line = testLine;
            }
        }
        let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
        ctx.strokeText(line, marginLeft, marginTop);
        ctx.fillText(line, marginLeft, marginTop);
        return canvas.toDataURL("image/png")
    }
    function getFontHeight(font) {
        let parent = document.createElement("span");
        parent.appendChild(document.createTextNode("height"));
        document.body.appendChild(parent);
        parent.style.cssText = "font: " + font + "; white-space: nowrap; display: inline;";
        let height = parent.offsetHeight;
        document.body.removeChild(parent);
        return height;
    }

    const storiesPost = () => {
        const blob = writeTop(zodiac.label + " - " + hososcopeArr.source + "\n" +hososcopeArr.sign_teaser.find(item=>item.sign === zodiac.name).text);
        const link = "https://vk.com/app7617614";
        bridge.send("VKWebAppShowStoryBox", {
            background_type: "image",
            url:
                "https://i.pinimg.com/736x/3b/3a/11/3b3a11b5dd823830adf94eba98cd9060.jpg",
            locked: true,
            attachment: {
                text: "view",
                type: "url",
                url: link,
            },
            stickers: [
                {
                    sticker_type: "renderable",
                    sticker: {
                        transform: {
                            relation_width: 0.9,
                            translation_y: 0.25,
                        },
                        can_delete: 0,
                        content_type: "image",
                        url: images.find(item=>item.label === zodiac.name).img,
                        clickable_zones: [
                            {
                                // "action_type": "app",
                                action_type: "link",
                                action: {
                                    link:link,
                                    tooltip_text_key: "tooltip_open_page",
                                },
                                clickable_area: [
                                    {
                                        x: 0,
                                        y: 0,
                                    },
                                    {
                                        x: 884,
                                        y: 0,
                                    },
                                    {
                                        x: 884,
                                        y: 196,
                                    },
                                    {
                                        x: 0,
                                        y: 196,
                                    },
                                ],
                            },
                        ],
                    },
                },
                {
                    sticker_type: "renderable",
                    sticker: {
                        transform: {
                            gravity: "center_top"
                        },
                        can_delete: 0,
                        content_type: "image",
                        blob: blob,
                        clickable_zones: [
                            {
                                // "action_type": "app",
                                action_type: "link",
                                action: {
                                    link:link,
                                    tooltip_text_key: "tooltip_open_page",
                                },
                                clickable_area: [
                                    {
                                        x: 0,
                                        y: 0,
                                    },
                                    {
                                        x: 884,
                                        y: 0,
                                    },
                                    {
                                        x: 884,
                                        y: 196,
                                    },
                                    {
                                        x: 0,
                                        y: 196,
                                    },
                                ],
                            },
                        ],
                    },
                },
            ],
        });
    };
    return hososcopeArr !== null && zodiac !== null ?
        <>
            <canvas
                style={{display: 'none'}}
                ref={canvasRef}
                width={window.innerWidth}
                height={window.innerHeight}
            />
        <Placeholder
            stretched
            icon={<img height={100} width={130} src={images.find(item=>item.label === zodiac.name).img}/>}
            header={zodiac.label + " - " + hososcopeArr.source}
            action={
                <div style={{display: 'flex'}}>
                    <Button onClick={wallPost} before={<Icon24ShareOutline />} mode={'outline'} stretched style={{marginRight: 8}}>На стену</Button>
                    <Button onClick={storiesPost} before={<Icon24StoryOutline />} mode={'outline'} stretched>В историю</Button>
                </div>
            }
        >
            {hososcopeArr.sign_teaser.find(item=>item.sign === zodiac.name).text}
        </Placeholder></> :
        <Loader/>
};

export default Horoscope;